:root { --mc-light : #00de7b; --mc-dusty : #00ca70; --mc-gradiant1 : #00ba6e; --red-light : #ff0031; --red-dusty : #ff4e4e; --red-dark : #ea333c; --gray-light : #ededed; --gray-dusty : #b6b6b6; --gray-dark : #b2b2b2; --black-dark : #1d1d1f; --bg: #fff; --blue: #2997ff; --dark-blue: #06c; --bright-gray: #fbfbfd; --ww-blue: #efefff; } 

html { font-size: 10px; color: var(--black-dark); } 
body { overflow-x: hidden; background: #fff; color: var(--black-dark); } 
/* .container { display: flex; margin: 0 auto; } */
.mob_add { display: none; } 
/* .wrap { } */

/* !------------------ 이 위로 공통 적용 style ------------------! */

/* * { background: rgba(250, 150, 150, 0.1); } */


/* !------------------ 탑배너 시작 ------------------! */
.Topbanner { background: var(--ww-blue); font-size: 14px; height: 54px; } 
.Topbanner.on { overflow: hidden; height: 0; transition: 0.3s; } 
.Topbanner .container { display: flex; padding: 20px 80px; } 
.Topbanner i { cursor: pointer; margin: auto 0 auto auto; } 
.tbText>span { margin: 0 0 0 2px; color: var(--dark-blue); } 
.tbText>span:hover { border-bottom: 1px solid var(--dark-blue); } 

/* !------------------ 헤더 시작 ------------------! */
/* .Header { } */
.Header h1 { width: 110px; margin-right: 80px; } 
.Header>.container { display: flex; padding: 20px 80px; } 
.gnb { margin: auto auto 0 0; } 
.gnb ul { display: flex; gap: 40px; } 
.gnb li { position: relative; font-size: 16px; } 
.gnb li::after { content: ''; position: absolute; bottom: -8px; height: 2px; left: 0; width: 100%; background-color: #000; transform: scaleX(0); transition: transform .33s cubic-bezier(0.694, 0.0482, 0.335, 1), } 
.gnb li:hover::after { transform: scaleX(100%); } 


/* !------------------ 메인 시작 ------------------! */
/* .Mainvisual { } */
.Mainvisual .container { position: relative; width: 1400px; margin: 0 auto; padding: 80px 0; } 
.Mainvisual .slick-slider { width: 960px; margin: 0 0 auto auto; } 
.Mainvisual figure { height: 580px; background-repeat: no-repeat; background-position: center center; background-size: cover; border-radius: 4px; } 
/* .Mainvisual figure.on { } */
.Mainvisual .itm01 { background-image: url(../public/assets/img/main_visual_01.jpg); } 
.Mainvisual .itm02 { background-image: url(../public/assets/img/main_visual_02.jpg); } 
.Mainvisual .itm03 { background-image: url(../public/assets/img/main_visual_03.jpg); } 
.Mainvisual .itm04 { background-image: url(../public/assets/img/main_visual_04.jpg); } 
.Mainvisual .slideArrows button { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); background: var(--bg); border: none; color: var(--lc); font-size: 30px; padding: 20px; line-height: 1; cursor: pointer; border-radius: 50%; color: var(--dark-blue); } 
.Mainvisual .slideArrows button:first-child { margin: 0 0 0 -260px; } 
.Mainvisual .slideArrows button:last-child { margin: 0 0 0 700px; } 
.Mainvisual .slideArrows button:hover:first-child,
.Mainvisual .slideArrows button:hover:last-child { background: var(--ww-blue); } 

.findWorkSpace { position: absolute; word-break:keep-all; top: 50%; left: 0; transform: translateY(-50%); } 